@import "../../Components/Global/global";

.contactParentWrapper {
  display: grid;
  grid-template-columns: 884px 1fr;
}

.logo {
  margin-top: 50px;
  margin-left: 215px;
}

.sideDrawerWrapper {
  margin-left: 30px;
  width: 380px;
}
.sideDrawerHeaderWrapper {
  display: flex;
  flex-direction: column;
}
.sideDrawerHeadingWrapper {
  display: flex;
  align-items: flex-start;
  column-gap: 17px;
  /*height: 45px; */
  align-items: flex-end;
}
.formContainter {
  padding-top: 75px;
}

.sideDrawerHeading {
  font-family: "Roboto";
  font-weight: 600;
  font-size: 36px;
  line-height: 42px;
  column-gap: 17px;
  color: #24272c;
}
.sideDrawerSubHeading {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 28px;
  margin-bottom: 20px;
  color: #282c32;
}
.sideDrawerTitle {
  font-size: 1.5rem;
  font-family: "Roboto";
  font-weight: 500;
  color: #24272c;
}

.inputBox1 {
  background-color: $inBgColor;
  margin-bottom: 24px;
  border-radius: 8px;
  height: 56px;
  width: 380px;
  outline: none;
  padding: 19px 24px 8px 24px;
  border: none;
  box-sizing: border-box;
  font-size: 15px;
}
.inputBoxContainer {
  position: relative;
}

.setupLink {
  text-decoration: none;
}

.inputLabel {
  position: absolute;
  z-index: 1;
  top: 19px;
  left: 24px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  color: #7b8aa2;

  &.active {
    top: 13px;
    font-family: "Roboto";
    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    line-height: 12px;
    /* identical to box height */

    color: #12b0ee;
  }
}
.inputBox2 {
  height: 56px;
  width: 380px;
  background-color: $inBgColor;
  margin-bottom: 6px;
  border-radius: 8px;
}

.inputHelperText {
  margin-left: 20px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: $inSecondaryColor;
  margin-bottom: 26px;
}

.secondHalf {
  min-width: 372px;
  height: 100vh;
  overflow-y: scroll;
}
.buttonWrapIcon {
  display: flex;
  background-color: $btBgColor;
  column-gap: 8px;
  justify-content: center;
  align-items: center;
  width: 116px;
  margin-top: 25px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  font-family: "Roboto";
  box-sizing: border-box;
  height: 56px;
  width: 234px;
  color: white;
  border-radius: 8px;
  box-shadow: 0px 8px 8px rgba(50, 197, 255, 0.1);
}

.backArrow {
  position: relative;
}

.selectButtonWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f2f3f7;
  border-radius: 8px;
  width: 100%;
  padding: 17px 16px;
  cursor: pointer;
  margin-bottom: 16px;
  width: 380px;
  height: 56px;
  font-family: "Roboto";
  box-sizing: border-box;
}

.disabled-text-color {
  color: #757575;
}

.active-text-color {
  color: black;
}

.option-atom {
  margin-bottom: 20px;
  cursor: pointer;
  font-family: "Roboto";
}

.select-option-wrapper {
  background: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  padding: 23px 43px;
  position: relative;
  top: -10px;
  box-sizing: border-box;
  width: 380px;
}

.firstHalf {
  background-color: #e5e9f2;
  height: 100vh;
  background-size: cover;
  background-image: url("../../assets/images/back.png");
}

@media (max-width: 992px) {
  .contactParentWrapper {
    display: grid;
    grid-template-columns: none;
    grid-template-rows: 147px 1fr;
    background-color: #f3f7f9;
  }
  .firstHalf {
    height: auto;
    background: none;
    background-color: #f3f7f9;
  }
  .secondHalf {
    background: #ffffff;
    box-shadow: 0px -1px 5px rgba(0, 0, 0, 0.15);
    border-radius: 24px 24px 0px 0px;
    width: auto;
  }
  .sideDrawerWrapper {
    margin-top: 18px;
    margin-left: 20px;
    margin-right: 16px;
    min-height: 60vh;
    width: auto;
  }
  .sideDrawerTitle {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    /* identical to box height, or 117% */

    color: #3b414a;
  }
  .selectButtonWrapper {
    width: auto;
  }
}

